import { useParams } from 'react-router-dom';
import { useGetCarByIdQuery, baseUrl } from '../../api/services/data';
import { moneyFormat } from '../../helpers/moneyFormat'
import { Pannellum } from '360-react-pannellum';
import ThreeSixty from 'react-360-view';
import { useState } from 'react';
import Icon360 from '../../icons/360-icon.svg';
import './Car.css';
import Loader from '../../Components/Loader/Loader';

const Car = () => {
    const [insideView, setInsideView] = useState(true)
    const params = useParams()
    const { data, error, isLoading } = useGetCarByIdQuery(params?.id)

    return (
        <div className="car">
            <h1 className="main-title car-main-title">
                Modellari
            </h1>

            {
                error ? (
                    <h2>Nimadir xatolik</h2>
                ) : isLoading ? (
                    <Loader />
                ) : (
                    <>
                        <div className="car-info">
                            <h2 className="title uppercase">
                                {data?.data?.marka?.name} Car
                            </h2>

                            <div className="col-half">
                                <p className="price">
                                    {moneyFormat(data?.data?.price)} so'm dan
                                </p>

                                <img
                                    src={baseUrl + data?.data?.imgUrl}
                                    alt="Car Image"
                                />
                            </div>

                            <div className='col-half'>
                                <p>
                                    <span>Marka:</span>
                                    <span className='uppercase'>
                                        {data?.data?.marka?.name}
                                    </span>
                                </p>
                                <p>
                                    <span>Tanirovkasi:</span> {data?.data?.tonirovka}
                                </p>
                                <p>
                                    <span>Motor:</span> {data?.data?.motor}
                                </p>
                                <p>
                                    <span>Year:</span> {data?.data?.year}
                                </p>
                                <p>
                                    <span>Color:</span> {data?.data?.color}
                                </p>
                                <p>
                                    <span>Distance:</span> {data?.data?.distance}
                                </p>
                                <p>
                                    <span>Gearbook:</span> {data?.data?.gearbok}
                                </p>
                                <p>
                                    <span>Desription:</span> {data?.data?.description}
                                </p>

                                <hr />

                                <p>
                                    <span>Umumiy xarajat:</span> {moneyFormat(data?.data?.price)} so'm dan
                                </p>
                            </div>
                        </div>

                        <div className="car-image-360">
                            <h2 className="title uppercase">
                                {data?.data?.marka?.name} Car
                            </h2>
                            {
                                insideView ? (
                                    <Pannellum
                                        width='100%'
                                        height='400px'
                                        imagePath={baseUrl + data?.data?.imgUrlInside}
                                        showZoomCtrl={false}
                                        showFullscreenCtrl={false}
                                        autoLoad
                                    />
                                ) : (
                                    // <h1>Outside View Image</h1>
                                    <ThreeSixty
                                        amount={72}
                                        // imagePath="https://scaleflex.cloudimg.io/crop/1920x700/n/https://scaleflex.airstore.io/demo/360-car"
                                        imagePath="https://cdn.kia-motors.uz/exterior/K5/UD/"
                                        fileName="{index}.png"
                                        buttonClass="dark"
                                        spinReverse={true}
                                    />
                                )
                            }

                            <div
                                className="icon-360"
                                style={{
                                    backgroundImage: `url(${Icon360})`
                                }}
                            >
                            </div>

                            <p className="warning-content">
                                Tasvir tanlangan konfiguratsiyaga mos kelmasligi mumkin. Mashinaning rangi ushbu saytda taqdim etilganidan farq qilishi mumkin.
                            </p>

                            <div className="select-inside-outside">
                                <label htmlFor="select-out">
                                    <input
                                        type="radio"
                                        name='car-select'
                                        id='select-out'
                                        checked={!insideView}
                                        onChange={() => setInsideView(!insideView)}
                                    /> Tashqi
                                </label>
                                <label htmlFor="select-inside">
                                    <input
                                        type="radio"
                                        name='car-select'
                                        id='select-inside'
                                        checked={insideView}
                                        onChange={() => setInsideView(!insideView)}
                                    /> Ichki makon
                                </label>
                            </div>
                        </div>
                    </>
                )
            }
        </div>
    )
}

export default Car;