import { useState, lazy, Suspense } from "react";
import { Provider } from "react-redux";
import { store } from './api/store';
import Navbar from "./Components/Navbar/Navbar";
import Models from "./Screens/Models/Models";
import Cars from "./Screens/Cars/Cars";
import Car from "./Screens/Car/Car";
import { Routes, Route, useParams, BrowserRouter, Navigate } from 'react-router-dom';
import './App.css';
import './fonts/NotoSans-Light.ttf';
import './fonts/NotoSans-Regular.ttf';
import './fonts/NotoSans-Thin.ttf';
import Layout from "./Screens/Layout/Layout";
import Login from "./Screens/Login/Login";

const initialLinks = ['Bosh sahifa', 'Modellar']

function App() {
  const [links, setLinks] = useState(initialLinks)

  const params = useParams();

  const changeLinks = links => {
    setLinks(links)
  }

  return (
    <Provider store={store}>
      <BrowserRouter>
        <div className="App">
          <Routes>
            <Route path="/" element={<Layout links={links} />}>
              <Route
                path="/"
                element={<Models initialLinks={initialLinks} changeLinks={changeLinks} />}
              />
              <Route
                path="/:model/:id"
                element={<Cars links={links} changeLinks={changeLinks} />}
              />
              <Route
                path="/car/:id"
                element={<Car links={links} changeLinks={changeLinks} />}
              />
            </Route>
            <Route
              path="/login"
              element={<Login />}
            />
            <Route path="/*" element={<Navigate to="/" replace />} />
          </Routes>
        </div>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
