import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

export const baseUrl = 'https://cartestwebapp.herokuapp.com/';

export const modelsApi = createApi({
    reducerPath: 'modelsApi',
    baseQuery: fetchBaseQuery({ baseUrl }),

    endpoints: builder => ({
        getAllModels: builder.query({
            query: () => ({
                url: 'category/marka?limit=10&page=1',
                method: 'GET'
            })
        }),

        getAllCarsByModelId: builder.query({
            query: id => ({
                url: `car?limit=8&page=1&categoryId=${id}`,
                method: 'GET'
            })
        }),

        getCarById: builder.query({
            query: id => ({
                url: `https://cartestwebapp.herokuapp.com/car/${id}`,
                method: 'GET'
            })
        }),
    })
})

export const {
    useGetAllModelsQuery,
    useGetAllCarsByModelIdQuery,
    useGetCarByIdQuery
} = modelsApi;