import { useGetAllModelsQuery } from '../../api/services/data';
import { baseUrl } from '../../api/services/data';
import { NavLink } from 'react-router-dom';
import { useEffect } from 'react';
import Loader from '../../Components/Loader/Loader'
import './Models.css';

const Models = ({ initialLinks, changeLinks }) => {
    const { data, error, isLoading } = useGetAllModelsQuery()
    useEffect(() => {
        changeLinks(initialLinks)
    })

    return (
        <div className="home">
            <h1 className="main-title">Modellari</h1>

            <div className="row">
                {
                    error ? (
                        <div className="error-status">
                            Qandaydir xatolik
                        </div>
                    ) : isLoading ? (
                        <Loader />
                    ) : (
                        !data?.data?.data.length ? (
                            <div className="info-status">
                                Hech qanday ma'lumot mavjud emas :(
                            </div>
                        ) : data?.data?.data?.map(model => (
                            <NavLink
                                to={`/${model?.name}/${model?._id}`}
                                className="item"
                                key={model?._id}
                            >
                                <img
                                    src={`${baseUrl}${model?.imgUrl}`}
                                    alt={`Image: ${model?.name}`}
                                    loading='lazy'
                                />
                                <h4 className="title">{model?.name}</h4>
                            </NavLink>
                        ))
                    )
                }
            </div>
        </div>
    )
}

export default Models;