import { useGetAllCarsByModelIdQuery, baseUrl } from "../../api/services/data";
import { useParams, NavLink } from 'react-router-dom';
import { useEffect } from "react";
import { moneyFormat } from '../../helpers/moneyFormat';
import './Cars.css';
import Loader from "../../Components/Loader/Loader";

const Cars = ({ links, changeLinks }) => {
    const params = useParams();
    const { data, error, isLoading } = useGetAllCarsByModelIdQuery(params?.id)

    useEffect(() => {
        const oldLinks = [...links]
        oldLinks[2] = `${params?.model} turlari`

        changeLinks(oldLinks)
    }, [])

    return (
        <div className="cars">
            <h1 className="main-title">
                {params?.model} turlari
            </h1>

            <div className="row">
                {
                    error ? (
                        <div className="error-status">
                            Qandaydir xatolik
                        </div>
                    ) : isLoading ? (
                        <Loader />
                    ) : (
                        !data?.data?.data.length ? (
                            <div className="info-status">
                                Hech qanday ma'lumot mavjud emas :(
                            </div>
                        ) : data?.data?.data?.map(car => (
                            <NavLink
                                to={`/car/${car?._id}`}
                                className="item"
                                key={car?._id}
                            >
                                <img
                                    src={`${baseUrl}${car?.imgUrl}`}
                                    alt={`Image: ${car?.marka?.name}`}
                                    loading='lazy'
                                />
                                <div>
                                    <h4 className="title">{car?.marka?.name}</h4>
                                    <h5 className="title price-title">
                                        Narxi: <span>{moneyFormat(car?.price + '')}</span>
                                    </h5>
                                </div>
                            </NavLink>
                        ))
                    )
                }
            </div>
        </div>
    )
}

export default Cars;